
import { Options, Vue } from "vue-class-component";

@Options({
  emits: {
    assemblyFnOne: null
  },
  components: {},
  props: {
    defaultStyle: {
      type: Object
    },
    idname: String
  }
})
export default class editShape extends Vue {
  mounted() {
    setTimeout(() => {
      this.$emit("assemblyFnOne");
    }, 0);
  }
}
